.blog {
  font-family: "Roboto", sans-serif;
  font-size: 10px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #ffffff;
  padding: 2rem 1.5rem;
  width: 100%;
  /*margin-left: 10px;
  margin-right: 30px; */
}

.blog-post {
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: #ffff;
  border-radius: 0.8em;
  padding-top: 0.1rem;
  padding-bottom: 1px;
  margin-top: -350px;

  font-family: "Calibri Light";
  font-size: 15px;
  font-weight: 400;
  color: #202020;
}

.blog-post-img {
  margin-left: 0.5rem;
}

.blog-post-img img {
  width: 130px;
  height: 130px;
  border-radius: 0.8rem;
}

.blog-post-img::before {
  content: "";
  width: 120px;
  height: 120px;
  top: 0;
  left: 0;
}

.blog-post-title h1 {
  color: rgba(200, 209, 27, 0.8);
  font-size: 1.2rem;
  padding-top: 0px !important;
  font-weight: 700;
  margin: 0.1rem 0;
}

.blog-post-title {
  font-size: 0.7rem;
  margin: 0.4rem 0.4rem;
  text-transform: uppercase;
  color: #182735;
}

.message {
  font-family: "Calibri Light";
  font-size: 15px;
  font-weight: 400;
  color: #202020;
  padding: 10px;
  line-height: 1.8;
}

@media screen and (max-width: 868px) {
  .blog-post2 {
    padding-top: 30rem;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 1rem;
    flex-direction: column;
  }
  .blog-post-img2 img {
    min-width: 170px;
    max-width: 100vh;
    transform: translate(0, -8rem);
  }
  .blog-post-info2 {
    margin-top: -50px;
    margin-left: -100px;
  }

  .blog-post2 .message2 {
    text-align: center;
  }
}
.blog-post2 .message2 {
  margin-top: 380px;
  padding-top: 200px;
  margin-left: -650px;
  padding-left: 90px;
  padding-right: 10px;
  padding-bottom: 20px;
  color: rgba(16, 17, 3, 0.8);
  line-height: 1.8;
  font-family: "Calibri Light";
  font-size: 15px;
  font-weight: 400;
  color: #202020;
}

.title2 {
  color: rgba(39, 39, 37, 0.8);
  margin-top: 20px;
  padding-bottom: 0px;
  padding: auto;
  margin-left: 100px;
  word-wrap: break-word;
  font-weight: 700px;
  font-size: 32px;
  color: #000;
}
